<template>
  <nav class="page-navigator">
    <a class="extend prev" rel="prev" :style="currentSelect==1?'visibility: hidden':'visibility: inherit'" @click="--currentSelect">上一页</a>

    <div>
<!--      <span class="page-number current">1</span>-->
<!--      <a class="page-number current">1</a>-->
<!--      <a class="page-number" href="/page/2/">2</a>-->

      <a v-for="pageIndex in pageCount" :class="pageIndex==currentSelect?'page-number current':'page-number'"  @click="currentSelect=pageIndex">{{pageIndex}}</a>
    </div>

    <a class="extend next" rel="next" :style="currentSelect==pageCount?'visibility: hidden':'visibility: inherit'" @click="++currentSelect">下一页</a>
  </nav>
</template>

<script>
  export default {
    name: "MyPagination",
    data() {
      return {
        title: 'home',
        currentSelect:1,   //当前选中项
      }
    },
    props: { //总页数、必传，接收父组件传过来的值
      pageCount: {
        type: Number,
        require: true,
      },
      pageSize: { //每页展示数目，默认每页5条数据
        type: Number,
        require: false,
        default: 5,
      },
    },
    methods: {
      onPageSelect(index) {
        this.currentSelect = index;
        this.$emit("onPageSelect", index);
      },
      filterDatas(){
        // 1  5  1 ;  最少7项  8？        9  10
        //8 ；     1     2 3 4 5 6 7      8
      },
    },
    watch:{
      currentSelect(val) {
        this.onPageSelect(val)
        // this.$emit("onPageSelect", index);
      }
    }
  }
</script>

<style scoped>
  .page-navigator {
    border-top: 1px solid #ddd;
    list-style: none;
    margin-top: 25px;
    padding: 25px 0 0;
    font-size: 14px;
    text-align: center;

    display: flex;
    justify-content: space-between;
  }

  .page-navigator .page-number {
    display: inline-block;
    margin: 0 5px 0px 0;
    color: #6E7173;
  }

  .page-navigator a, .page-navigator span {
    display: inline-block;
    min-height: 25px;
    line-height: 25px;
    padding: 5px 9px;
    border: 1px solid #DDD;
    text-align: center;
  }

  .page-navigator a:hover, .page-navigator span:hover {
    background: #F8F8F8;
    border-bottom-color: #D26911;
  }

  .page-navigator a.prev, .page-navigator span.prev {
    float: left;
  }

  .page-navigator a.prev:before, .page-navigator span.prev:before {
    font-family: "FontAwesome";
    content: "\f100";
    padding-right: 0.3em;
  }

  .page-navigator a.next, .page-navigator span.next {
    float: right;
  }

  .page-navigator a.next:after, .page-navigator span.next:after {
    font-family: "FontAwesome";
    content: "\f101";
    padding-left: 0.3em;
  }

  .page-navigator .current {
    background: #F8F8F8;
    border-bottom-color: #D26911;
  }

  .page-navigator .space {
    border: none;
    padding: 5px 5px;
  }

  .page-navigator a {
    color: #6e7173;
  }
  .page-navigator a:hover {
    color: #555;
  }
  .page-navigator a:active {
    color: #444;
  }
</style>



