<template>
  <div>
    <h2>new/edit   posts</h2>
  <div  class="select-bg">
    <span style="margin-left: 20px;">请输入标题：</span> <a-input placholder="请输入标题" allow-clear v-model="blogTitle" style="width: 300px;"></a-input>
    <a-button style="margin-left: 50px;" type="primary" @click="isEdit = !isEdit">编辑已发布文章</a-button>
    <div style="display: inline-block" v-show="isEdit">
      <a-input placholder="请输入blogId" v-model="blogId" placeholder="blog id" type="number" style="margin-left: 25px;width: 100px;"></a-input>
      <a-button style="margin-left: 10px;" @click="getBlogInfo">获取文章</a-button>
    </div>
  </div>

  <div class="select-bg">
    <span class="select-label">请选择分类：</span>
<!--    <a-select default-value="1" style="width: 120px" @change="handleTypeChange">-->
<!--    <a-select v-model="blogType"  default-value="blogType" style="width: 120px" @change="handleTypeChange">-->
    <a-select  v-model="blogType" style="width: 120px" @change="handleTypeChange">
      <a-select-option v-for="item in selectList" :key="item.stype">
        {{item.name}}
      </a-select-option>
    </a-select>
    <span class="select-label" style="display: none">标签：</span>
    <a-select v-model="blogType" style="width: 120px;display: none" @change="handleChange">
      <a-select-option v-for="item in selectList" :value="item.stype">
        {{item.name}}
      </a-select-option>
    </a-select>
      <span class="select-label">是否发布：</span>
      <a-select v-model="blogPublish" style="width: 120px;" @change="handlePublicChange">
      <a-select-option value="0">
        NO
      </a-select-option>
      <a-select-option value="1">
        published
      </a-select-option>
    </a-select>

    <span class="select-label" style="margin-right: 0px">时间：</span>
    <a-date-picker
      v-model="cTime"
      @change="onTimeChange"
      :mode="mode1"
      show-time
      @openChange="handleOpenChange1"
      @panelChange="handlePanelChange1"
    />

  </div>

  <div id="div1" class="blog-edit">
    <quill-editor
      :content="content"
      :options="editorOption"
      @change="onEditorChange($event)"
      style="height: 500px"
    />

  </div>

  <div style="margin-top: 100px;">
    <a-button  @click="formatContent">check</a-button>
    <a-button @click="addBlog" type="danger">submit</a-button>
    <div style="margin-top: 15px">{{contentSubmit}}</div>
  </div>


  <div class="login-wrap" v-show="isShowLogin">
    <div  class="login-bg">
      <span style="margin-top: 15px;font-weight: bold">登录</span>
      <a-input placholder="请输入标题" allow-clear v-model="userName" style="width: 270px;margin-top: 15px"></a-input>
      <a-input placholder="请输入标题" allow-clear v-model="passWord" style="width: 270px;margin-top: 15px"></a-input>
      <a-button style="margin-top: 20px" @click="doLogin">登录</a-button>
    </div>

  </div>

  </div>
</template>

<script>
  // import E from 'wangeditor'
  // const editor = new E('#div1');
  // editor.create();

  // import {mavonEditor} from "mavon-editor";
  // import "mavon-editor/dist/css/index.css";

  import {quillEditor} from 'vue-quill-editor';
  import {getTypes, addBlogs, updateBlogs, getBlogInfo} from "../api/request";
  import {message} from 'ant-design-vue';
  // import yourQuillModule from '../yourModulePath/yourQuillModule.js'
  // Quill.register('modules/yourQuillModule', yourQuillModule)

    export default {
        name: "BlogEidt",
      data(){
          return{
            blogId:'--',
            blogTitle:'',
            blogType:'1',
            // blogType:{status:1},
            cTime:undefined,
            blogPublish:'1',

            isEdit:false,//true: 编辑状态  false：非编辑状态
            mode1: 'time',
            doc:'fdgdfg',
            content: '',
            editorOption: {
              // Some Quill options...
            },
            contentSubmit:'',

            selectList:[],

            isShowLogin:true,
            userName:'',
            passWord:'',
          }
      },
      // components:{mavonEditor},
      components:{quillEditor},
      mounted() {
          this.getSelects();
      },
      methods:{
        getSelects(){
          var _this = this;
          getTypes(location.href.split("#")[0]).then((response) => {
            _this.selectList = response.selects;
            // _this.blogType = 1;
          }).catch((err)=>{
          });
        },

        addBlog(){
          let _this = this;
          if (_this.blogTitle == ''){
            message.warning('标题不能为空！');
            return;
          }
          if (_this.cTime == '' || _this.cTime == undefined){
            message.warning('请选择时间！');
            return;
          }
          if (_this.content == ''){
            message.warning('内容不能为空！');
            return;
          }


          if (isNumber(_this.blogId)){

          }else{

            let blog = {
              // id:_this.blogId,
              title:_this.blogTitle,
              ctime:_this.cTime.format('YYYY-MM-DD HH:mm:ss'),
              type:_this.blogType,
              label:_this.blogType,
              content:_this.content,
              publish:_this.blogPublish,
            };


            addBlogs(blog).then((response) =>{
              if (response.code==200){
                message.info('操作成功！')
              }else{
                message.error('操作失败，请重试！')
              }
              console.log(response)
            }).catch((err)=>{

            });
          }


        },

        getBlogInfo(){
          var that = this;
          getBlogInfo(that.blogId).then((response) => {
            if (response.code==200){
              that.blogTitle = response.data.title;

              try {
                that.cTime = that.$moment(response.data.ctime,'YYYY-MM-DD HH:mm:ss');
                console.log(that.$moment(response.data.ctime,'YYYY-MM-DD HH:mm:ss'));
              }catch (e) {
                console.log(e);
              }
              // that.cTime = moment(response.data.ctime,'YYYY-MM-DD HH:mm:ss');

              that.blogType = response.data.type.toString();
              that.blogPublish = response.data.publish.toString();
              that.content = response.data.content;
              // that.blog = response.data;
            }
          }).catch((err)=>{
          });
        },

        doLogin(){
          if (this.userName==''){
            message.error('用户名不能为空');
            return;
          }
          if (this.passWord==''){
            message.error('密码不能为空');
            return;
          }
          this.isShowLogin=false;
        },

        formatContent(){
          this.contentSubmit = this.content;

        },

        handleTypeChange(value) {

          console.log(`type selected ${value}`);
          // this.blogType = value;
          console.log('this.blogType '+ this.blogType);
        },
        handlePublicChange(value) {
          console.log(`type selected ${value}`);
        },
        handleChange(value) {
          console.log(`selected ${value}`);
        },

        onTimeChange(date, dateString) {
          // console.log(date, dateString);
          // this.cTime = dateString;
          console.log(this.cTime);
          console.log(this.cTime.format('YYYY-MM-DD HH:mm:ss'));
        },

        handleOpenChange1(open) { //弹出日历和关闭日历的回调
          if (open) {
            this.mode1 = 'time';
          }
        },
        handlePanelChange1(value, mode) { //日期面板变化时的回调
          this.mode1 = mode;
          console.log(this.mode1+'---'+value)
        },
        onEditorChange({ quill, html, text }) {
          // console.log('editor change!', quill, html, text)
          this.content = html
        }

      },
      computed: {
        editor() {
          return this.$refs.myQuillEditor.quill
        }
      },
    }
  function isNumber(obj) {
    return obj === +obj
  }
</script>

<style scoped>
  .blog-edit{
    margin: 20px 25px;
    /*min-height: 500px;*/
    height: auto;
  }
  .ql-editor {
    min-height: 200px;

  }
  .select-bg{
    margin: 10px 5px;
    text-align: start;
  }
  .select-bg .select-label{
    margin: 0 0px 0 18px;

  }
/*span{*/
/*}*/

  .login-wrap{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9);

    z-index: 999;

    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;
  }
  .login-wrap .login-bg{
      background: #FFFFFF;
      border-radius: 5px;
      height: 200px;
      width: 300px;
      text-align: center;
    padding-top: 15px;
  }

</style>



