var pageStore = {
  debug: true,
  state:{
    message: 'Hello!',
  },
  pageState: {
    message: 'Hello!',
    home: 11,
    android: 5,
    html5: 2,
    javaWeb: 1,
    tools: 2,
    life: 2,
    beyond: 1
  },
  setMessageAction (newValue) {
    if (this.debug) console.log('setMessageAction triggered with', newValue)
    this.state.message = newValue
  },
  clearMessageAction () {
    if (this.debug) console.log('clearMessageAction triggered')
    this.state.message = ''
  },

  setPageAction (home,android,html5,javaWeb,tools,life,beyond) {
    try {
      this.pageState.home = home;
      this.pageState.android = android;
      this.pageState.html5 = html5;
      this.pageState.javaWeb = javaWeb;
      this.pageState.tools = tools;
      this.pageState.life = life;
      this.pageState.beyond = beyond;
    }catch (e) {
      console.log('初始化page异常'+JSON.stringify(e));
    }
  },


}
export {pageStore}



