<template>
    <div id="blog-list">
      <div v-for="item in blogList" class="post"  @click="gotoRead(item.number)">
        <h1 class="post-title"><a>{{item.title}}</a></h1>
        <div class="post-meta">{{item.created_at | formatGithubTime}}</div>
        <div class="post-content">
          <div v-html="item.body" class="show-content" style="overflow: hidden">
<!--            {{item.content}}-->

<!--            <div style="clear: both">-->

<!--            </div>-->
          </div>
        </div>
        <p class="readmore" ><a>阅读全文</a></p>
      </div>

      <my-pagination :page-count="countBlog" @onPageSelect="onPageSelect"></my-pagination>
    </div>
</template>

<script>
  import MyPagination from "../components/MyPagination";
  import {getBlogList,gitapiBlogList, getPagesConfig} from "../api/request";
  import {pageStore} from "../store/pageStore";

  // import {mapState} from 'vuex' //注册action和state
    export default {
      name: "BlogList",
      // computed: {
      //     ...mapState([
      //       'count','pages',
      //     ]),
      //     count (){
      //       return store.state.count;
      //     },
      //     pages (){
      //       return store.state.pages;
      //     }
      // },
      data(){
          return{
            // blogList:null,
            blogList:[{'title':'□□□□□','created_at':'2021-08-09','body':'□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□','number':1},
              {'title':'□□□□□','created_at':'2021-08-09','body':'□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□','number':1},
              {'title':'□□□□□','created_at':'2021-08-09','body':'□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□□','number':1}],
            countBlog:1,
          }
      },
      components: {
        MyPagination
      },
      mounted() {
          this.getBlogList(1);
          this.getPageConfig();
      },
      methods:{
        getPageConfig(){
          var _this = this;
          console.log(location.href)
          getPagesConfig(location.href.split("#")[0]).then((response) => {
            console.log('getPagesConfig'+JSON.stringify(response));
            //此处后期更换为vuex，此时是简单的store模式
            pageStore.setPageAction(response.home,response.android,response.html5,response.javaWeb,
              response.tools,response.life,response.beyond);

            console.log('bloglist-----'+pageStore.pageState.home);
            _this.countBlog = pageStore.pageState.home;
          }).catch((err)=>{
            console.log('catch  getPagesConfig');
          });
        },
        getBlogList(pageIndex){
          var _this = this;
          gitapiBlogList(pageIndex,5).then((response) => {
              _this.blogList = response;
            console.log(_this.countBlog);
            // if (response.code==200){
            // }
          }).catch((err)=>{
          });

        },
        gotoRead(id){
          // this.$parent.currentContent = detail;
          // this.$parent.selectTab(10);
          // this.$router.push({name:"blogContent",params:{id:id}})
          this.$router.push({name:"blogContent",query:{id:id}})
        },
        onPageSelect(index){
          this.getBlogList(index);
          console.log('parent-getPage'+index)
        }
      },
    }
</script>

<style scoped>
  .post {
    padding: 25px 0 15px; }
  .post .post-title {
    margin: 0;
    color: #555;
    text-align: left;
    font: bold 25px/1.1 "ff-tisa-web-pro", Cambria, "Times New Roman", Georgia, Times, sans-serif; }
  .post .post-title a {
    color: #555; }
  .post .post-meta {
    padding: 0;
    margin: 15px 0 0;
    color: #6E7173;
    float: left;
    display: inline;
    text-indent: .15em; }
  .post .post-meta:before {
    font-family: "FontAwesome";
    content: "\f073";
    padding-right: 0.3em; }
  .post .post-meta .category:before {
    font-family: "FontAwesome";
    content: "\f07c";
    padding-right: 0.3em; }
  .post .post-meta #busuanzi_value_page_pv:before {
    font-family: "FontAwesome";
    content: "\f024";
    padding-right: 0.3em; }
  .post .ds-thread-count {
    padding: 0;
    margin: 15px 0 0;
    color: #6E7173;
    float: right;
    display: inline;
    text-indent: .15em; }
  .post .ds-thread-count:before {
    font-family: "FontAwesome";
    content: "\f0e5";
    padding-right: 0.3em; }
  .post .ds-thread-count:hover {
    color: #444; }
  .post .disqus-comment-count {
    padding: 0;
    margin: 15px 0 0;
    color: #6E7173;
    float: right;
    display: inline;
    text-indent: .15em; }
  .post .disqus-comment-count:before {
    font-family: "FontAwesome";
    content: "\f0e5";
    padding-right: 0.3em; }
  .post .disqus-comment-count:hover {
    color: #444; }
  .post .post-content {
    clear: left;
    font-size: 15px;
    line-height: 1.77;
    color: #444;
    padding-top: 15px;
    text-align: justify;
    text-justify: distribute;
    word-break: break-all;
    overflow: hidden;

  }
  .show-content {
    max-height: 260px;
    /*max-width: 800px;*/
    overflow: hidden;

    background-image: linear-gradient(top,rgba(255,255,255,0), rgba(255,255,255,1));
  }
  .show-content >>> img,p,span,div {
    /*max-width: 900px;*/
    /*max-width: 800px;*/
    /*width: 900px;*/
  }

  .post .post-content h2 {
    margin: 1.4em 0 1.1em;
    border-bottom: 1px solid #eee;
    overflow: hidden; }
  .post .post-content h3 {
    margin: 1.4em 0 1.1em; }
  .post .post-content pre code {
    padding: 0 2em; }
  .post .post-content p {
    /*margin: 0 0 1.234em; */
    max-width: 1000px;

  }
  .post .post-content p code {
    display: inline-block;
    margin: 0 5px;
    padding: 0 5px;
    background: #f7f8f8; }
  .post .post-content p a {
    color: #01579f;
    padding-bottom: 2px;
    word-break: break-all; }
  .post .post-content p a:hover {
    text-decoration: underline; }
  .post .post-content .caption {
    color: #444;
    display: block;
    font-size: 0.9em;
    margin-top: 0.1em;
    position: relative;
    text-align: center; }
  .post .post-content hr {
    margin: 2.4em auto;
    border: none;
    border-top: 1px solid #eee;
    position: relative; }
  .post .post-content img {
    max-width: 100%;
    padding: 0.5em 0;
    margin: auto;
    display: block; }
  .post .post-content ul, .post .post-content ol {
    border-radius: 3px;
    margin: 1em 0; }
  .post .post-content ul ul, .post .post-content ol ul {
    margin: 0; }
  .post .post-content ul code, .post .post-content ol code {
    display: inline-block;
    margin: 0 5px;
    padding: 0px 5px;
    background: #f7f8f8; }
  .post .post-content ul a, .post .post-content ol a {
    color: #01579f;
    padding-bottom: 2px;
    word-break: break-all; }
  .post .post-content ul a:hover, .post .post-content ol a:hover {
    text-decoration: underline; }
  .post .tags {
    padding-bottom: 1em; }
  .post .tags a {
    margin-right: .5em; }
  .post .tags a:before {
    font-family: "FontAwesome";
    content: "\f0c6";
    padding-right: 0.3em; }

  /* read more*/
  .readmore a {
    font-size: 14px;
    color: #444;
    margin: 5px 0 0 0;
    padding: 5px 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    float: right; }
  .readmore a:after {
    font-family: "FontAwesome";
    content: "\f101";
    padding-left: 0.3em; }
  .readmore a:hover {
    background: #F8F8F8;
    border-bottom-color: #D26911; }

  #blog-list a {
    color: #6e7173;
  }
  #blog-list a:hover {
    color: #555;
  }
  #blog-list a:active {
    color: #444;
  }

  .show-content a{
    color: #6e7173;
  }
</style>



