import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home';
import main from '@/pages/main'
import blogList from '@/pages/blogList'
import Test from '@/pages/Test'
import editBlog from '@/pages/BlogEdit'
// import blogContent from '@/pages/BlogContent'


Vue.use(Router)

export default new Router({
  base:'/samon/',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      // redirect: '/main',
      redirect: '/blogList',
      children : [
        {
          path: '/',
          name: 'blogList',
          component: () => import('@/pages/blogList'),
          meta: { title: '首页', hiddenHeaderContent: false }
        },
        {
          path: '/blogArchive',
          name: 'blogArchive',
          component: () => import('@/pages/BlogArchive'),
          meta: { title: '归档', hiddenHeaderContent: false }
        },
        {
          path: '/about',
          name: 'about',
          component: () => import('@/pages/About'),
          meta: { title: '关于', hiddenHeaderContent: false }
        },
        {
          path: '/statistical',
          name: 'statistical',
          component: () => import('@/pages/Statistical'),
          meta: { title: '设置', hiddenHeaderContent: false }
        },
        {
          path: '/blogContent',
          name: 'blogContent',
          component: () => import('@/pages/BlogContent'),
          meta: { title: '详情', hiddenHeaderContent: false }
        }
      ]
    },
    {
      path: '/main',
      name: 'main',
      component: main
    },
    {
      path: '/blogList',
      name: 'blogList',
      component: blogList
    },
    {
      path: '/test',
      name: 'test',
      component: Test
    },
    {
      path: '/editBlog',
      name: 'editBlog',
      component: editBlog
    },
  ]
})



