<template>
  <div id="header">
    <div class="site-name"><h1 class="hidden">Samon</h1><a id="logo" href="/.">Samon</a>
      <p class="description">to be a better man</p></div>
    <div id="nav-menu">
      <a :class="currentTab==1 ? 'current':''" @click="tapTab(1)"><i class="fa fa-home"> 首页</i></a>
      <a :class="currentTab==2 ? 'current':''" @click="tapTab(2)"><i class="fa fa-archive"> 归档</i></a>
      <a :class="currentTab==3 ? 'current':''" @click="tapTab(3)"><i class="fa fa-user"> 关于</i></a>
      <a :class="currentTab==4 ? 'current':''" @click="tapTab(4)"><i class="fa fa-coffee"> 更多</i></a></div>

    <div class="nav-letter welcome">{{welcome}}</div>

  </div>
</template>

<script>
  export default {
    name: "BlogHeader",
    data(){
      return{
        currentTab:1,
        welcome:'hello world!',
        nowIndex:0,
      }
    },
    props:{
      currentApi:{ //当前api环境  0：自己服务器api  1：github apis
        type:Number,
        required:false,
        default:0,
      }
    },
    mounted() {
      let _this = this;
      this.getHeaderConfig()

      let timer = setInterval(function () {
        _this.nowIndex++;
        switch (_this.nowIndex) {
          case 1:
            _this.welcome = 'Log.e("◕‿-");';
            break;
          case 2:
            _this.welcome = 'System.out.println(".");';
            break;
          case 3:
            _this.welcome = 'console.log("♩♪♫♬♭ ◕‿◕");';
            break;
          case 4:
            _this.welcome = 'printf("✐✎✏✑✒/n");';
            break;
        }
        if (_this.nowIndex > 4){
          _this.nowIndex = 1;
        }

      },5000);

    },
    methods:{
      tapTab(index){
        var _this = this;
        this.currentTab = index;
        console.log(_this.currentApi)
        let mUrl = '';
        if (_this.currentApi == 1){
          mUrl = 's';
        }


        if (index == 1) {
          _this.$router.push({ name: mUrl+'blogList' },
            onComplete=>{},
            onAbort=>{})
        } else if (index == 2) {
          _this.$router.push({ name: mUrl+'blogArchive' },onComplete=>{},
            onAbort=>{})
        } else if (index == 3) {
          _this.$router.push({ name: 'about' },
            onComplete=>{},
            onAbort=>{})
        } else if (index == 4) {
          _this.$router.push({ name: 'statistical' },
            onComplete=>{},
            onAbort=>{})
        }
      },
      getHeaderConfig(){
        // https://my-json-server.typicode.com/songmeng/songmeng.github.io/posts
        // https://my-json-server.typicode.com/songmeng/songmeng.github.io/posts?_page=2&_limit=2

      },
      getC(){
      }
    },
  }

</script>

<style scoped>
  #header {
    padding: 50px 0 0;
    text-align: left;
    border-bottom: 1px solid #ddd;
    position: relative; }
  #header .site-name {
    margin-bottom: 40px; }
  #header .site-name h1 {
    padding: 0;
    margin: 0;
    height: 0;
    overflow: hidden; }
  #header .site-name #logo {
    font: bold 38px/1.12 "Times New Roman", Georgia, Times, sans-serif;
    color: #555; }
  #header .site-name #logo span, #header .site-name #logo:hover {
    color: #777; }
  #header .site-name .description {
    margin: .2em 0 0;
    color: #999; }
  #header #nav-menu {
    margin: 10px 0 -1px;
    padding: 0;
    position: absolute;
    right: 0;
    bottom: 0; }
  #header #nav-menu a {
    display: inline-block;
    padding: 3px 20px 3px;
    line-height: 30px;
    color: #444;
    font-size: 13px;
    border: 1px solid transparent; }
  #header #nav-menu a:hover {
    border-bottom-color: #444; }
  #header #nav-menu a.current {
    border: 1px solid #ddd;
    border-bottom-color: #fff; }

  #header .nav-letter{
    margin: 0px 0 -1px;
    padding: 0;
    position: absolute;
    left: 0;
    bottom: 0;

    /*height: 45px;*/
    /*width: 100px;*/

    margin:0;
    font-family: Arial, Helvetica, sans-serif;
  }
  #header .welcome{
    color: #999;
    /*text-transform: uppercase;*/
    text-align: start;
    font-size: 12px;
    line-height: 1;
    animation:letterspacing 4s 700 alternate ease-in-out;
    display: block;
    letter-spacing: 0.1rem;

    height: 30px;
    line-height: 30px;
  }
  @keyframes letterspacing {
    20% {
      /*letter-spacing: -72px;*/
      letter-spacing: -7px;
      filter: blur(20px);
    }

    50% {
      filter: blur(6px);
    }

    80% {
      /*letter-spacing: 8px;*/
      letter-spacing: 18px;
      filter: blur(0px);
    }
  }

</style>



