<template>
  <div class="">
    <div id="sidebar">
      <div class="widget">
        <form class="search-form" action="//www.baidu.com/baidu" method="get" accept-charset="utf-8"
              target="_blank"><input type="search" name="word" maxlength="20" placeholder="Search"><input
          type="hidden" name="si" value="http://yoursite.com"><input name="tn" type="hidden" value="bds"><input
          name="cl" type="hidden" value="3"><input name="ct" type="hidden" value="2097152"><input name="s" type="hidden" value="on"></form>
      </div>
      <div class="widget">
        <div class="widget-title"><i class="fa fa-folder-o"> 分类</i></div>
        <ul class="category-list">
          <li class="category-list-item" v-for="item in categoryBlog">
            <router-link :to="{name:'blogArchive',query:{id:item.stype,name:item.name}}" class="category-list-link" >{{item.name}}</router-link>
          </li>
        </ul>
      </div>
      <div class="widget">
        <div class="widget-title"><i class="fa fa-star-o"> 标签</i></div>
        <div class="tagcloud">

          <router-link  v-for="item in tagList" :to="{name:'blogArchive',query:{id:item.slabel,name:item.name}}" style="font-size: 15px;" >{{item.name}}</router-link>

        </div>
      </div>
      <div class="widget">
        <div class="widget-title"><i class="fa fa-file-o"> 最近文章</i></div>
        <ul class="post-list">

          <li class="post-list-item" v-for="item in latestBlog">
            <router-link :to="{name:'blogContent',query:{id:item.number}}" class="post-list-link" >{{item.title}}</router-link>
          </li>
        </ul>
      </div>
      <div class="widget">
        <div class="widget-title"><i class="fa fa-external-link"> 友情链接</i></div>
        <a href="https://5161718.com/" title="site-name1" target="_blank">javaer</a>
        <a href="https://5161718.com/" title="site-name2" target="_blank">5161718.com</a>
        <a href="https://github.com/songmeng/Sunburst" title="site-name3" target="_blank">Sunburst</a></div>
    </div>
  </div>
</template>

<script>
  import {getBlogList, getTypes, gitapiBlogList} from "../api/request";

  export default {
        name: "BlogCategory",
      data(){
          return{
            categoryBlog:[{name:'Android11',type:'1'},{name:'Html5',type:'2'},{name:'Tools',type:'3'}],
            tagList:[{name:'beyond11',type:'2'},{name:'生活',type:'3'},{name:'life',type:'44'}],
            latestBlog:null,
          }
      },
      props:{
        currentApi:{ //当前api环境  0：自己服务器api  1：github apis
          type:Number,
          required:false,
          default:0,
        }
      },
      mounted() {

        this.getLatestBlog();
        this.getTypes();
      },
      methods:{
        getTypes(){
          var _this = this;
          console.log(location.href)
          getTypes(location.href.split("#")[0]).then((response) => {
            _this.categoryBlog = response.types;
            _this.tagList = response.labels;
          }).catch((err)=>{
          });
        },
        getLatestBlog(){
          var _this = this;
          gitapiBlogList(1,0).then((response) => {
            _this.latestBlog = response;
          }).catch((err)=>{
          });
        },
      }
    }
</script>

