<template>
  <div>
    <div>
      <label>总页数</label>
      <input style="width: 20%" type="number" placeholder="总页数" v-model="num">
      <label>当前选中</label>
      <input style="width: 20%" type="number" placeholder="当前选中" v-model="select">
      <label>展示页数</label>
      <input style="width: 20%" type="number" placeholder="展示页数" v-model="pageSize">
      <span @click="doPrint">calculate</span>
    </div>
    <div>
      {{result}}
    </div>

    <div>
      <i class="fa fa-leanpub" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Test",
        data(){
            return{

              num:8,  //总页数

              select:5, //当前选中页

              pageSize:7,  //展示的页数，大于1

              result:'',
            }
        },
      methods:{
          doPrint(){

            if (this.num){
              let str = '';
              let begin = parseInt(this.select)-parseInt(this.pageSize/2);
              let end = parseInt(this.select)+ parseInt(this.pageSize/2);

              console.log(this.select+']['+parseInt(this.pageSize/2)+'限制为'+begin+'---'+end);
              for (let i = 1; i <= this.num; i++) {
                if (i==1 || i==this.num ){
                  str += '  '+ i.toString();
                }else if (i>=begin && i<= end ){
                  if (i==begin && begin>2){
                    str += ' ... ';
                  }else if (i==end && end<this.num-1){
                    str += ' ... ';
                  }else{
                    str += '  '+ i.toString();
                  }


                  // if (i==2 || 1==this.num-1){
                  //   str += '  ...';
                  // }else{
                  //
                  // }
                }
              }
              this.result = str;
              console.log(str)





              // if (this.num > this.pageSize){
              //   if (this.select>(this.pageSize -3)){
              //     let str = '';
              //     str += '  1   ';
              //     str += ' ... ';
              //
              //
              //     for (let i = this.select-2; i <= this.num; i++) {
              //       str += '  '+ i.toString();
              //     }
              //     console.log(str)
              //   }else{
              //
              //   }
              //
              //
              // }else{
              //   let str = '';
              //   for (let i = 1; i <= this.num; i++) {
              //     str += '-'+ i.toString();
              //   }
              //   console.log(str)
              // }




            }else{
              this.result = 'nothing';
              console.log('nothing')
            }


          }
      }
    }
</script>

