import {Service} from './Service.js';
// 和 Service.js 搭配使用

export function getConfigsByProductId(productId) {
  return Service({
    url: '/manager/getConfigsByProductId',
    params: { productId: productId }
  })
}
export function getAllAndroidPlugins() {
  return Service({
    url: '/manager/getAndroidPlugin ',
    method: 'get'
  })
}
export function addNewAndroidPlugin(data) {
  return Service({
    url: '/manager/addAndroidPlguin',
    data: JSON.stringify(data)
  })
}

export function getTest(data) {
  return Service({
    // url: '../config/test.json',
    // url: '/static/test.json',
    url: '../static/test.json',
    method: 'get'
  })
}
export function getTypes(rootUrl) {
  return Service({
    url: rootUrl+'../static/blog_config.json',
    method: 'get'
  })
}
export function getPagesConfig(rootUrl) {
  return Service({
    // url: rootUrl+'../static/pages_config.json',
    url: 'https://my-json-server.typicode.com/songmeng/Sunburst/pages_total',
    method: 'get'
  })
}
export function getLabels() {
  return Service({
    url: '../static/test.json',
    method: 'get'
  })
}


export function getBlogList(pageSize,pageIndex,type) {
  return Service({
    url: 'blogSamon/posts/posts_list?size='+pageSize+'&page='+pageIndex+(type==undefined? '' :'&type='+type),
    method: 'get'
  })
}
export function getBlogInfo(id) {
  return Service({
    url: 'blogSamon/posts/posts_info?id='+id,
    method: 'get'
  })
}
export function addBlogs(data) {
  return Service({
    url: 'blogSamon/posts/add_posts',
    data: JSON.stringify(data),
    method: 'post'
  })
}
export function updateBlogs(data) {
  return Service({
    url: 'blogSamon/posts/update_posts',
    data: JSON.stringify(data),
    method: 'post'
  })
}


/*  github apis  https://api.github.com/repos/songmeng/Sunburst/issues?state=all&page=5&per_page=3 */
//https://api.github.com/repos/martin-liu/martin-liu.github.io/issues?labels=blog&page=1&per_page=10&state=open
export function gitapiBlogList(pageIndex,pageSize,type) {
  return Service({
    url: 'https://api.github.com/repos/songmeng/Sunburst/issues?author=songmeng&state=all&per_page=6&page='+pageIndex+(type==undefined? '' :'&labels='+type),
    method: 'get'
  })
}

//
export function gitapiBlogInfo(id) {
  return Service({
    url: 'https://api.github.com/repos/songmeng/Sunburst/issues/'+id,
    method: 'get'
  })
}






